.App {
  text-align: center;
}

.App-intro {
  margin: 10% 10%;
}

.App-smallContent {
  font-size: 1rem;
  color: pink;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-block {
  text-align: left;
  margin: 10% 10%;
  padding: 30px;
  font-size: 1rem;
  border: 1px dotted white;
  line-height: 1.5rem;
}




